@import 'src/common/styles/sass/variables';
@import '../common/components/button/Button.module';

.contactsBlock {
  background-color: $backgroundColor2;
  min-height: 50vh;

  .contactsContainer {
    border: 1px solid white;
    min-height: 50vh;
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .title {
      background-color: #DDDEDE;
    }

    .formContainer {
      width: 70%;
      //overflow: hidden;
      //box-sizing: border-box;

      .form {
        display: flex;
        flex-direction: column;
        align-items: center;

      }

      .input {
        width: 100%;
        margin-bottom: 5px;
        background: #252527 !important;
        border: 1px solid #4a4a4a;
        font-size: 12px;
        padding: 0 15px;
        border-radius: 0;
        height: calc(1.5em + .75rem + 2px);
        font-weight: 400;
        line-height: 1.5;
        color: $textColor;
      }

      .textarea {
        width: 100%;
        min-height: 140px;
        padding: 15px;
        background: #252527 !important;
        border: 1px solid #4a4a4a;
        font-size: 12px;
        border-radius: 0;
        font-weight: 400;
        line-height: 1.5;
        color: $textColor;
      }

      .button[type="submit"] {
        @extend .btn;
        background-color: transparent;
        margin: 30px 0 40px 0;
      }

      .disabledButton[type="submit"] {
        @extend .btn;
        background-color: transparent;
        margin: 30px 0 40px 0;

        &::after {
          width: 100%;
          height: 110%;
          transition: all 0.3s ease-in-out 0s;
        }
      }
    }
  }
}
