@import "src/common/styles/sass/variables";

.workBlock {
  width: 460px;
  background-color: #d9d8d8;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  @media screen and (max-width: 1156px) {
    width: 400px;
  }
  @media screen and (max-width: 1030px) {
    width: 510px;
    margin-bottom: 20px;
  }

  .icon {
    width: 100%;
    height: 240px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
    background-position: center;
    position: relative;

    &::before { // псевдоэлемент
      position: absolute;
      content: '';
      background-color: black;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      opacity: 0.3; // прозрачность
    }

    .button {
      color: white;
      background-color: black;
      position: relative;
      z-index: 1;
    }
  }

  .description {
    background-color: $backgroundColor2;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 25px 20px;

    .projectName {
      color: $textColor;
      font-size: 16px;
      letter-spacing: 1px;
      margin: 0 0 5px;
      font-weight: 700;
      line-height: 1.2;
      font-family: 'Josefin Sans', sans-serif;
    }

    .shortDescription {
      color: $textColor;
      font-weight: 400;
      font-size: 14px;
      line-height: 1.8;
      font-family: "Poppins", sans-serif;
    }
  }
}



